<template>
   <div class="binding">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>{{arr[index].title}}绑定</span>
        </div>
        <div class='bin_cen'>
            <van-cell-group>
                <van-field
                style='height:48px;'
                label='昵称'
                v-model="Name"
                ref="Name"
                placeholder="请输入昵称" />
            </van-cell-group>
            <van-cell-group>
                <van-field
                style='height:48px;'
                label='账号'
                v-model="User"
                ref="User"
                placeholder="请输入账号" />
            </van-cell-group>
        </div>
        <van-button 
        size="large" 
        style='position:absolute; 
        bottom:0; 
        color:white; 
        background:#00a0e9;' 
        @click='binding()'
        >立即绑定</van-button>
   </div>
</template>

<script>
export default {
   name: 'binding',
   data() {
       return {
           index:this.$route.query.num,
           arr:[
               { title:'支付宝' },
               { title:'微信' }
           ],
           Name:'',
           User:''
       }
   },
   methods:{
        retreat(){
            this.$router.go(-1);
        },
        binding(){
            if( this.index == 0 ){
                let url = '/userLogin/setAlipay';
                this.$http.post(url,{
                    cmd:'102',
                    body:{
                        accessToken:localStorage.getItem('token'),
                        did:'1',
                        appid:'11',
                        nickname:this.Name,
                        account:this.User
                    }
                }).then((res) => {
                    this.$router.go(-1);
                    // console.log(res);
                })
            }else if( this.index == 1 ){
                let url = '/userLogin/setWechat';
                this.$http.post(url,{
                    cmd:'102',
                    body:{
                        accessToken:localStorage.getItem('token'),
                        did:'1',
                        appid:'11',
                        nickname:this.Name,
                        account:this.User
                    }
                }).then((res) => {
                    this.$router.go(-1);
                    // console.log(res);
                })
            }
        }
   },
  components: {}
}
</script>

<style scoped lang='scss'>
.binding{
    width: 100%;
    font-size: 16px;
    height: calc(100vh);
    background: white;
    .mine_top{
        height: 64px;
        background: white;
        text-align: center;
        line-height: 64px;
        color: black;
        position: relative;
        border-bottom: 1px solid #ddd;
        .return{
            position: absolute;
            left: 8px;
            width: 32px;
            height: 64px;
            background: url('../../../static/images/icon/arrows3.png') no-repeat center;
            background-size: 24px;
        }
    }
    .bin_cen{
        width: 100%;
    }
}
</style>
